/**
 *
 * @type {{Inv: string, FlatRates: string, Accounts: string, Category: string, Resolution_Codes: string, JobType: string, Multi_Travel_Fields: string}}
 */
const CREATE_TABLE = {
    'Level': 'CREATE TABLE IF NOT EXISTS Level(id INTEGER NOT NULL PRIMARY KEY AUTOINCREMENT, sid INTEGER, Screen VARCHAR, Name VARCHAR, Label VARCHAR)',
    'Phone': 'CREATE TABLE IF  NOT EXISTS Phone(id INTEGER NOT NULL PRIMARY KEY AUTOINCREMENT, sid INTEGER,Rol INTEGER, Loc INTEGER, fDesc VARCHAR, Phone VARCHAR, Fax VARCHAR, Title VARCHAR, Cell VARCHAR, Email VARCHAR, EmailRecInvoice INTEGER, EmailRecQuote INTEGER, EmailRecTicket INTEGER, EmailRecPO INTEGER, updated INTEGER Default 0 )',
    'Alert': 'CREATE TABLE IF  NOT EXISTS tblAlerts(id INTEGER NOT NULL PRIMARY KEY AUTOINCREMENT, AlertID INTEGER, ScreenID INTEGER, ScreenName VARCHAR, AlertCode VARCHAR)',
    'Alert_Answer': 'CREATE TABLE IF  NOT EXISTS Alert_Answer(id INTEGER NOT NULL PRIMARY KEY AUTOINCREMENT, alert_id INTEGER, ticket_id INTEGER, answer TEXT)',
    'work_order': 'CREATE TABLE IF  NOT EXISTS work_order(id INTEGER NOT NULL PRIMARY KEY AUTOINCREMENT,' +
        ' loc INTEGER, job_id INTEGER, job_desc VARCHAR, created_date DATETIME, code VARCHAR, phase VARCHAR, hours' +
        ' VARCHAR, hours_percentage ' +
        ' VARCHAR, wage VARCHAR, ticket_id INTEGER, member_id INTEGER, work_order VARCHAR, time_card_id INTEGER, crews' +
        ' INTEGER DEFAULT 0, status INTEGER DEFAULT 1, sign TEXT,"wo_hours" VARCHAR, "wo_hours_comp_tickets" INTEGER' +
        ' DEFAULT 0,' +
        ' "last_ct_lead" VARCHAR)',
    'tbl_work': 'CREATE TABLE IF  NOT EXISTS tbl_work(id INTEGER NOT NULL PRIMARY KEY AUTOINCREMENT, user_id INTEGER,' +
        ' user_name VARCHAR, data TEXT)',
    'tbl_crew': 'CREATE TABLE IF  NOT EXISTS tbl_crew(id INTEGER NOT NULL PRIMARY KEY AUTOINCREMENT, member_id' +
        ' INTEGER, work_order_table_id INTEGER, work_order VARCHAR, ticket_id INTEGER, data TEXT, status INTEGER DEFAULT' +
        ' 1)',
    "multiple_equipments": "CREATE TABLE IF  NOT EXISTS multiple_equipments(id INTEGER NOT NULL PRIMARY KEY" +
        " AUTOINCREMENT, ticket_id" +
        " INTEGER, elev_id INTEGER, labor_percentage INTGER DEFAULT 0)",

    'AlertUserData': 'CREATE TABLE IF NOT EXISTS AlertUserData(id INTEGER NOT NULL PRIMARY KEY AUTOINCREMENT,' +
        ' alertCode VARCHAR, sId INTEGER, alertUserInput TEXT, updated INTEGER DEFAULT 0)',
    'ticket_codes': 'CREATE TABLE IF NOT EXISTS ticket_codes(id INTEGER NOT NULL PRIMARY KEY AUTOINCREMENT,' +
        ' ticket_id INTEGER, code VARCHAR, type INTEGER, Category VARCHAR, job_id INTEGER, default_code INTEGER DEFAULT' +
        ' 0, last_updated_by VARCHAR, last_updated_date DATETIME, workorder VARCHAR)',
    'Sales_Source': 'CREATE TABLE IF  NOT EXISTS Sales_Source(id INTEGER NOT NULL PRIMARY KEY AUTOINCREMENT,' +
        ' salesSourceID VARCHAR, fDesc VARCHAR)',

    'Inv': 'CREATE TABLE IF NOT EXISTS Inv (id INTEGER NOT NULL PRIMARY KEY AUTOINCREMENT, "iName" VARCHAR NULL, "ifDesc" VARCHAR NULL, data TEXT NOT NULL,c_date DATE NOT NULL,"iKey" INTEGER, "Hand" INTEGER)',
    'FlatRates': 'CREATE TABLE IF NOT EXISTS FlatRates(id INTEGER NOT NULL PRIMARY KEY AUTOINCREMENT, data TEXT NOT NULL,c_date DATE NOT NULL)',
    'Accounts': 'CREATE TABLE IF NOT EXISTS Accounts(id INTEGER NOT NULL PRIMARY KEY AUTOINCREMENT, data TEXT NOT NULL,c_date DATE NOT NULL,"aID" VARCHAR, "aTag" VARCHAR,"aAddress" VARCHAR,"aCity" VARCHAR,"aPhone" VARCHAR,"aLoc" INTEGER,"aType" VARCHAR,"aMaint" VARCHAR,"rol_name" VARCHAR,"cellular" VARCHAR,DispAlert INTEGER DEFAULT 0)',
    'Category': 'CREATE TABLE IF NOT EXISTS Category(id INTEGER NOT NULL PRIMARY KEY AUTOINCREMENT, data TEXT NOT NULL,c_date DATE NOT NULL)',
    'Resolution_Codes': 'CREATE TABLE IF NOT EXISTS Resolution_Codes(id INTEGER NOT NULL PRIMARY KEY AUTOINCREMENT, data TEXT NOT NULL,c_date DATE NOT NULL)',
    'JobType': 'CREATE TABLE IF NOT EXISTS JobType(id INTEGER NOT NULL PRIMARY KEY AUTOINCREMENT, data TEXT NOT NULL,c_date DATE NOT NULL)',
    'Multi_Travel_Fields': 'CREATE TABLE IF NOT EXISTS Multi_Travel_Fields(id INTEGER NOT NULL PRIMARY KEY' +
        ' AUTOINCREMENT, data TEXT NOT NULL,c_date DATE NOT NULL)',

    'ToDo': 'CREATE TABLE IF  NOT EXISTS ToDo(id INTEGER NOT NULL PRIMARY KEY AUTOINCREMENT,' +
        ' sID VARCHAR, Type INTEGER, Rol INTEGER, fDate DATETIME, fTime DATETIME, DateDue DATETIME, TimeDue DATETIME,' +
        ' Subject VARCHAR, Remarks TEXT, Keyword VARCHAR, Level INTEGER, fUser INTEGER, fBy VARHCAR, Duration INTEGER,' +
        ' Contact VARCHAR, Source VARCHAR, CreatedBy VARCHAR, CreateDate DATETIME, LastUpdatedBy VARCHAR, LastUpdateDate' +
        ' DATE, updated INTEGER, StartDate DATETIME, EndDate DATETIME, Address VARCHAR, AllDay VARCHAR)',

    'Unavailable': 'CREATE TABLE IF  NOT EXISTS Unavailable(id INTEGER NOT NULL PRIMARY KEY AUTOINCREMENT, sID' +
        ' INTEGER, fDate DATETIME, Worker INTEGER, fDesc VARCHAR, AllDay VARCHAR, StartTime VARCHAR, EndTime VARCHAR,' +
        ' Remarks TEXT)'
};




