import React, { useState, useEffect, useRef } from "react";


const checkValueIsNotEmpty = (value) => {
    if(typeof value!='undefined' && value!=null && value!='null' && value!=''){
        return value;
    }else{
        return false;
    }
}

const getCurrentDateTime = (formatValue) => {
    var date = new Date(),
        dtvalue, month = ["Jan", "Feb", "Mar", "Apr", "May", "Jun", "Jul", "Aug", "Sep", "Oct", "Nov", "Dec"];
    date.setDate(date.getDate());

    var hours = date.getHours(),
        hours_full = hours,
        minute = date.getMinutes(),
        seconds = date.getSeconds();

    var period = hours >= 12 ? "PM" : "AM";

    hours = ((hours > 12) ? hours - 12 : hours);

    if (formatValue !== '') {
        if (formatValue === 'YYYY-MM-DD')
            dtvalue = date.getFullYear() + '-' + PadDigits((date.getMonth() + 1), 2) + '-' + PadDigits(date.getDate(), 2);

        else if (formatValue === 'MM/DD/YYYY')
            dtvalue = PadDigits((date.getMonth() + 1), 2) + '/' + PadDigits(date.getDate(), 2) + '/' + date.getFullYear();

        else if (formatValue === 'YYYY-MM-DD HH:MM:SS')
            dtvalue = date.getFullYear() + '-' + PadDigits((date.getMonth() + 1), 2) + '-' + PadDigits(date.getDate(), 2) + ' ' + PadDigits(hours_full, 2) + ':' + PadDigits(minute, 2) + ':' + PadDigits(seconds, 2);

        else if (formatValue === 'YYYY-MM-DD HH:MM:SS TT')
            dtvalue = date.getFullYear() + '-' + PadDigits((date.getMonth() + 1), 2) + '-' + PadDigits(date.getDate(), 2) + ' ' + PadDigits(hours, 2) + ':' + PadDigits(minute, 2) + ':' + PadDigits(seconds, 2) + ' ' + period;

        else if (formatValue === 'MM/DD/YYYY HH:MM TT')
            dtvalue = PadDigits((date.getMonth() + 1), 2) + '/' + PadDigits(date.getDate(), 2) + '/' + date.getFullYear() + ' ' + PadDigits(hours, 2) + ':' + PadDigits(minute, 2) + ' ' + period;

        else if (formatValue === 'MMM dd YYYY')
            dtvalue = month[date.getMonth()] + ' ' + date.getDate() + ' ' + date.getFullYear();

    } else
        dtvalue = PadDigits((date.getMonth() + 1), 2) + '/' + PadDigits(date.getDate(), 2) + '/' + date.getFullYear() + ' ' + PadDigits(hours, 2) + ':' + PadDigits(minute, 2) + ' ' + period;

    return dtvalue;
} 

const PadDigits = (n, totalDigits) => {
    n = n.toString();
    var pd = '';
    if (totalDigits > n.length) {
        for (var i = 0; i < (totalDigits - n.length); i++) {
            pd += '0';
        }
    }
    return pd + n.toString();
}

/**
 * Determine the mobile operating system.
 * This function returns one of 'iOS', 'Android', 'Windows Phone', or 'unknown'.
 *
 * @returns {String}
 */
function getMobileOperatingSystem() {
    var userAgent = navigator.userAgent || navigator.vendor || window.opera;
  
        // Windows Phone must come first because its UA also contains "Android"
      if (/windows phone/i.test(userAgent)) {
          return "Windows Phone";
      }
  
      if (/android/i.test(userAgent)) {
          return "Android";
      }
  
      // iOS detection from: http://stackoverflow.com/a/9039885/177710
      if (/iPad|iPhone|iPod/.test(userAgent) && !window.MSStream) {
          return "iOS";
      }
  
      return "unknown";
  }

export {
    getMobileOperatingSystem,
    checkValueIsNotEmpty,
    getCurrentDateTime
}

