import React, { useState, useRef } from 'react'
import {Navbar, Nav, Button, Container, Row, Col, Modal} from 'react-bootstrap'

//import SignaturePad from "react-signature-canvas";
import SignatureCanvas from 'react-signature-canvas'

import Camera from 'react-html5-camera-photo';
import 'react-html5-camera-photo/build/css/index.css';

import Header from './Header';

import {getRequest, sendRequest} from '../utils/httpservice';

const Home = () => {
  const [imageURL, setImageURL] = useState(null); // create a state that will contain our image url

  const [camURL, setCamURL] = useState(null); // create a state that will contain our image url

  const sigCanvas = useRef({});

  const [show, setShow] = useState(false);

  const [showc, setShowC] = useState(false);

  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  const handleShowC = () => setShowC(true);
  const handleCloseC = () => setShowC(false);

  /* a function that uses the canvas ref to clear the canvas 
  via a method given by react-signature-canvas */
  const clear = () => sigCanvas.current.clear();

  /* a function that uses the canvas ref to trim the canvas 
  from white spaces via a method given by react-signature-canvas
  then saves it in our state */
  const save = () =>{
    setImageURL(sigCanvas.current.getTrimmedCanvas().toDataURL("image/png"));
    handleClose();
  }
    

  function handleTakePhoto (dataUri) {
    // Do stuff with the photo...
    console.log('takePhoto');
    console.log(dataUri);
    setCamURL(dataUri);
    handleCloseC();
  }


  return (
    <>
    <Container fluid="md">
    <Header></Header>
  
  <Row>
    <Col> &nbsp;</Col>
    <Col> &nbsp;</Col>
    <Col> &nbsp;</Col>
  </Row>
  <Row>
      <Col>
      {/* <Button variant="primary" onClick={handleShow}>Capture Signature</Button>{' '}
      <Button variant="primary" onClick={handleShowC}>Capture Pictures</Button>{' '} */}


      </Col>
  </Row>


  <Modal show={show} onHide={handleClose}>
        <Modal.Header closeButton>
          <Modal.Title>Signature Ticket #123</Modal.Title>
        </Modal.Header>
        <Modal.Body>

        <SignatureCanvas penColor='black'
    canvasProps={{width: 500, height: 200, className: 'sigCanvas'}} ref={sigCanvas} />

        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={handleClose}>
            Close
          </Button>
          <Button variant="danger" onClick={clear}>
            Clear
          </Button>
          <Button variant="primary" onClick={save}>
            Save Changes
          </Button>
        </Modal.Footer>
      </Modal>


      {imageURL ? (
        <img
          src={imageURL}
          alt="my signature"
          style={{
            display: "block",
            margin: "0 auto",
            border: "1px solid black",
            width: "350px",
            marginTop:"50px"
          }}
        />
      ) : null}


<Modal show={showc} onHide={handleCloseC}>
        <Modal.Header closeButton>
          <Modal.Title>Signature Ticket #123</Modal.Title>
        </Modal.Header>
        <Modal.Body>

        <Camera
      onTakePhoto = { (dataUri) => { handleTakePhoto(dataUri); } }
      style={{width:"100%"}}
    />

        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={handleCloseC}>
            Close
          </Button>
          
          <Button variant="primary">
            Save Changes
          </Button>
        </Modal.Footer>
      </Modal>

      {camURL ? (
        <img
          src={camURL}
          alt="my clicked pictures"
          style={{
            display: "block",
            margin: "0 auto",
            border: "1px solid black",
            width: "350px",
            marginTop:"50px"
          }}
        />
      ) : null}
  </Container>
  
    </>
  )
}

export default Home
