import React, {useState} from 'react'
import {Navbar, Nav, Button, Container, Row, Col, Modal} from 'react-bootstrap'
const Header = ({activevalue}) => {
    const [activeTickets, setActiveTickets] = useState('');
    const [activeHome, setActiveHome] = useState('');
    switch(activevalue){
        case "tickets":
            setActiveTickets("active")
            break;
        case "home":
            setActiveHome("active")
            break;
        default:
            break;
    }
    return (
        <Navbar bg="primary" variant="dark">
    <Navbar.Brand href="#home">Navbar</Navbar.Brand>
    <Nav className="mr-auto">
      <Nav.Link href="/home" {...activeHome}>Dashboard</Nav.Link>
      <Nav.Link href="/tickets" {...activevalue === 'tickets' ? "active" : ''}>Tickets</Nav.Link>
      <Nav.Link href="#pricing">Options</Nav.Link>
    </Nav>
  </Navbar>
    )
}

export default Header
